import React, { useState } from 'react';
import StyledHeading from '../components/StyledHeading';
import { AiOutlineLeft, AiOutlineRight } from 'react-icons/ai'; 
import ClientImage1 from '../images/client1.jpg';
import ClientImage2 from '../images/client2.jpg';
import ClientImage3 from '../images/client3.jpg';
import ClientImage4 from '../images/client4.jpg';
import ClientImage5 from '../images/client5.jpg';

const testimonials = [
  {
    id: 1,
    text: "Wsppraca z t firm bya niezwykle profesjonalna. Zesp wykaza si ogromnym zaangaowaniem i kreatywnoci, dostarczajc projekt na czas i speniajc wszystkie nasze oczekiwania. Na pewno bdziemy wsppracowa ponownie.",
    author: "Anna Kowalska",
    image: ClientImage1,
  },
  {
    id: 2,
    text: "Jestem bardzo zadowolony z usug tej firmy. Projekt zosta zrealizowany zgodnie z harmonogramem, a jako pracy bya na najwyszym poziomie. Komunikacja bya szybka i skuteczna. Polecam kademu!",
    author: "Jan Nowak",
    image: ClientImage2,
  },
  {
    id: 3,
    text: "Doskonaa wsppraca, profesjonalizm w kadym calu. Zesp nie tylko zrealizowa nasz projekt, ale take doradzi najlepsze rozwizania techniczne. Wynik kocowy przeszed nasze oczekiwania.",
    author: "Katarzyna Malinowska",
    image: ClientImage3,
  },
  {
    id: 4,
    text: "Firma dostarczya projekt na czas i w peni spenia nasze oczekiwania. Zdecydowanie polecam ich usugi, zarwno pod ktem jakoci pracy, jak i komunikacji. Dzikujemy za wspania wspprac!",
    author: "Tomasz Winiewski",
    image: ClientImage4,
  },
  {
    id: 5,
    text: "wietna wsppraca, zesp wykaza si duym profesjonalizmem i zaangaowaniem. Zawsze dostpni, otwarci na wszelkie sugestie. Efekt kocowy by doskonay, zdecydowanie polecam!",
    author: "Ewa Zieliska",
    image: ClientImage5,
  },
];

const TestimonialsSection = () => {
  const [index, setIndex] = useState(0);

  const nextTestimonial = () => setIndex((index + 1) % testimonials.length);
  const prevTestimonial = () => setIndex((index - 1 + testimonials.length) % testimonials.length);

  return (
    <section className="p-12 bg-gradient-to-b from-gray-50 to-gray-200 text-gray-800">
        <div className="mb-12">
            <StyledHeading>Opinie Klientw</StyledHeading>
            <p className="mt-4 text-lg text-gray-600 text-center">
                Poznaj opinie naszych zadowolonych klientw, ktrzy zaufali nam i skorzystali z naszych usug budowlanych.
            </p>
        </div>

      <div className="max-w-2xl mx-auto text-center relative">
        {/* Strzaki po lewej i prawej stronie */}
        <button
          onClick={prevTestimonial}
          aria-label="Poprzednia opinia"
          className="absolute left-[-70px] top-1/2 transform -translate-y-1/2 p-4 rounded-full bg-transparent border-2 border-yellow-500 text-yellow-500 hover:bg-yellow-500 hover:text-white transition duration-300 hidden md:flex"
        >
          <AiOutlineLeft size={30} />
        </button>

        <div className="w-full p-8 bg-white shadow-lg rounded-lg transition-transform duration-500 ease-in-out transform hover:scale-105" aria-live="polite">
          {/* Zdjcie autora opinii */}
          <div className="flex justify-center mb-4">
            <img
              src={testimonials[index].image}
              alt={`Zdjcie autora opinii - ${testimonials[index].author}`}
              className="w-24 h-24 rounded-full object-cover shadow-md"
              loading="lazy"
            />
          </div>
          <p className="italic text-xl text-gray-600">"{testimonials[index].text}"</p>
          <p className="mt-4 font-semibold text-gray-900">- {testimonials[index].author}</p>
        </div>

        <button
          onClick={nextTestimonial}
          aria-label="Nastpna opinia"
          className="absolute right-[-70px] top-1/2 transform -translate-y-1/2 p-4 rounded-full bg-transparent border-2 border-yellow-500 text-yellow-500 hover:bg-yellow-500 hover:text-white transition duration-300 hidden md:flex"
        >
          <AiOutlineRight size={30} />
        </button>
      </div>

      {/* Wskaniki */}
      <div className="flex justify-center mt-6 space-x-2">
        {testimonials.map((_, i) => (
          <span
            key={i}
            className={`w-3 h-3 rounded-full ${i === index ? 'bg-yellow-500' : 'bg-gray-300'} transition duration-300 cursor-pointer`}
            onClick={() => setIndex(i)}
            onKeyDown={(e) => e.key === 'Enter' && setIndex(i)}
            tabIndex="0"
            aria-label={`Wybierz opini ${i + 1}`}
            aria-current={i === index ? "true" : undefined}
          ></span>
        ))}
      </div>
    </section>
  );
};

export default TestimonialsSection;





