import React, { useState } from 'react';
import StyledHeading from '../components/StyledHeading';
import HomeIcon from '../images/home.png';
import OfficeIcon from '../images/office.png';
import WrenchIcon from '../images/wrench.png';
import DesignIcon from '../images/design.png';
import RenovationIcon from '../images/renovation.png';
import ConsultationIcon from '../images/consultation.png';

const ServicesSection = () => {
  const [expanded, setExpanded] = useState(null);

  const toggleExpand = (index) => {
    setExpanded(expanded === index ? null : index);
  };

  return (
    <section id="services" className="p-8 sm:p-16 bg-gray-50 text-gray-800">
      {/* Nagwek sekcji */}
      <div className="text-center mb-12">
        <StyledHeading>Specjalizacja</StyledHeading>
        <p className="mt-4 text-lg text-gray-600">
          Z nami zrealizujesz kady projekt budowlany  od planowania po finalizacj. Sprawd nasze usugi.
        </p>
      </div>

      {/* Karty specjalizacji */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 max-w-6xl mx-auto">
        {[
          { icon: HomeIcon, title: 'Budowa domw jednorodzinnych', description: 'Tworzymy nowoczesne, trwae i przyjazne dla rodowiska domy, speniajc oczekiwania naszych klientw.' },
          { icon: OfficeIcon, title: 'Budynki komercyjne', description: 'Projektujemy i budujemy obiekty komercyjne dostosowane do potrzeb kadego biznesu.' },
          { icon: WrenchIcon, title: 'Remonty i wykoczenia wntrz', description: 'Zapewniamy kompleksowe remonty i wykoczenia wntrz z dbaoci o najmniejsze detale.' }
        ].map((service, index) => (
          <div
            key={index}
            className="relative bg-white p-8 rounded-lg shadow-lg transition-transform transform hover:-translate-y-2 cursor-pointer flex flex-col"
            onClick={() => toggleExpand(index)}
            onKeyDown={(e) => e.key === 'Enter' && toggleExpand(index)}
            role="button"
            tabIndex="0"
            aria-expanded={expanded === index}
            aria-label={`${service.title} - ${expanded === index ? 'Zwi szczegy' : 'Rozwi szczegy'}`}
          >
            <img src={service.icon} alt={`Ikona - ${service.title}`} className="w-16 h-16 mb-4 mx-auto opacity-90" loading="lazy" />
            <h3 className="text-2xl font-semibold mb-4">{service.title}</h3>

            {/* Tre podstawowa karty */}
            <p className={`text-gray-700 mb-4 transition-all duration-300 ease-in-out ${expanded === index ? 'max-h-full' : 'max-h-[3.6em] overflow-hidden'}`}>
              {service.description}
            </p>

            {/* Dodatkowe informacje pokazane po klikniciu */}
            {expanded === index && (
              <div className="mt-2 text-gray-600">
                <p className="text-sm">Nasza oferta obejmuje rwnie konsultacje, projektowanie i pene wsparcie na kadym etapie projektu.</p>
              </div>
            )}

            {/* Ikona rozwijania */}
            <div
              className={`absolute bottom-4 right-4 text-yellow-500 transform transition-transform duration-300 ${expanded === index ? 'rotate-180' : ''}`}
              aria-label={expanded === index ? "Poka mniej o usudze" : "Poka wicej o usudze"}
            >
              
            </div>
          </div>
        ))}
      </div>

      {/* Dodatkowe specjalizacje */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 mt-16 max-w-6xl mx-auto">
        {[
          { icon: DesignIcon, title: 'Projektowanie wntrz', description: 'Tworzymy funkcjonalne i estetyczne wntrza dostosowane do potrzeb klienta.' },
          { icon: RenovationIcon, title: 'Renowacje', description: 'Modernizujemy wntrza i budynki, dbajc o kady detal.' },
          { icon: ConsultationIcon, title: 'Konsultacje techniczne', description: 'Zapewniamy pene wsparcie techniczne na kadym etapie budowy.' }
        ].map((service, index) => (
          <div key={index} className="bg-white p-6 rounded-lg shadow-md transition-transform transform hover:-translate-y-2 cursor-pointer">
            <img src={service.icon} alt={`Ikona - ${service.title}`} className="w-12 h-12 mb-4 mx-auto" loading="lazy" />
            <h4 className="text-xl font-semibold mb-2">{service.title}</h4>
            <p className="text-gray-600">{service.description}</p>
          </div>
        ))}
      </div>
    </section>
  );
};

export default ServicesSection;








