import React from 'react';
import { Link } from 'react-scroll';
import BackgroundImage from '../images/construction-bg.jpg';

const WelcomeSection = () => {
  return (
    <section
      id="home"
      className="h-screen w-full bg-cover bg-center relative flex items-center justify-center text-white text-center pt-20 md:pt-0"
      style={{ backgroundImage: `url(${BackgroundImage})`, backgroundAttachment: 'fixed' }}
    >
      {/* Warstwa pprzezroczysta */}
      <div className="absolute inset-0 bg-black bg-opacity-60" aria-hidden="true"></div>

      {/* Tre sekcji */}
      <div className="relative z-10 p-6 sm:p-8 max-w-3xl">
        <h1 className="text-3xl text-white sm:text-5xl font-extrabold drop-shadow-lg mb-4">
          Budujemy solidne fundamenty dla Twojej przyszoci
        </h1>
        <p className="mt-4 text-base sm:text-lg italic text-gray-300 drop-shadow-md max-w-lg mx-auto">
          Od lat oferujemy kompleksowe usugi budowlane na najwyszym poziomie, specjalizujc si w realizacji projektw dostosowanych do indywidualnych potrzeb klientw.
        </p>
        <div className="mt-6 flex flex-col sm:flex-row sm:justify-center gap-4 sm:gap-6">
          <Link to="portfolio" smooth={true} duration={500} aria-label="Przejd do naszych projektw">
            <button className="px-6 py-2 text-sm sm:text-base bg-transparent border-2 border-yellow-500 text-yellow-500 rounded-full hover:bg-yellow-500 hover:text-white transition-all duration-300 ease-in-out">
              Zobacz nasze projekty
            </button>
          </Link>
          <Link to="contact" smooth={true} duration={500} aria-label="Skontaktuj si z nami">
            <button className="px-6 py-2 text-sm sm:text-base bg-transparent border-2 border-green-500 text-green-500 rounded-full hover:bg-green-500 hover:text-white transition-all duration-300 ease-in-out">
              Skontaktuj si z nami
            </button>
          </Link>
        </div>
        <div className="mt-8 text-xs sm:text-sm text-gray-400">
          <p className="max-w-md mx-auto">
            Wspieramy Ci na kadym etapie - od projektu po realizacj. Naszym priorytetem jest jako i terminowo.
          </p>
        </div>
      </div>
    </section>
  );
};

export default WelcomeSection;






